import React, { useState, useEffect } from 'react';
import {
  Button,
  TextField,
  IconButton,
} from '@mui/material';
import DeleteIcon from '@mui/icons-material/Delete';
import AppBar from '@mui/material/AppBar';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import { DataGrid } from '@mui/x-data-grid';


function App() {
  const [books, setBooks] = useState([]);
  const [newBook, setNewBook] = useState({
    title: '',
    author: '',
    year: '',
    isbn: '',
    price: ''
  });

  useEffect(() => {
    fetchBooks();
  }, []);

  const fetchBooks = async () => {
    try {
      const response = await fetch('https://todolist-1a6d8-default-rtdb.firebaseio.com/books/.json');
      if (!response.ok) {
        throw new Error('Failed to fetch books');
      }
      const data = await response.json();
      if (data) {
        const booksArray = Object.keys(data).map(key => ({
          id: key,
          ...data[key]
        }));
        setBooks(booksArray);
      } else {
        setBooks([]);
      }
    } catch (error) {
      console.error('Error fetching books:', error);
    }
  };

  const addBook = async () => {
    try {
      const response = await fetch('https://todolist-1a6d8-default-rtdb.firebaseio.com/books/.json', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify(newBook)
      });
      if (!response.ok) {
        throw new Error('Failed to add book');
      }

      setNewBook({
        title: '',
        author: '',
        year: '',
        isbn: '',
        price: ''
      });

      fetchBooks();
    } catch (error) {
      console.error('Error adding book:', error);
    }
  };

  const deleteBook = async (bookId) => {
    try {
      const response = await fetch(`https://todolist-1a6d8-default-rtdb.firebaseio.com/books/${bookId}.json`, {
        method: 'DELETE'
      });
      if (!response.ok) {
        throw new Error('Failed to delete book');
      }
      fetchBooks();
    } catch (error) {
      console.error('Error deleting book:', error);
    }
  };

  const clearBooks = async () => {
  // Remove all books from Firebase
  try {
    const response = await fetch('https://todolist-1a6d8-default-rtdb.firebaseio.com/books/.json', {
      method: 'DELETE'
    });
    if (!response.ok) {
      throw new Error('Failed to clear books');
    }
    // Clear books from local state
    setBooks([]);
  } catch (error) {
    console.error('Error clearing books:', error);
  }
};


  const columns = [
    { field: 'title', headerName: 'Title', flex: 1 },
    { field: 'author', headerName: 'Author', flex: 1 },
    { field: 'year', headerName: 'Year', flex: 1 },
    { field: 'isbn', headerName: 'ISBN', flex: 1 },
    { field: 'price', headerName: 'Price', flex: 1 },
    {
      field: 'action',
      headerName: 'Action',
      flex: 1,
      renderCell: (params) => (
        <IconButton
          size="small"
          color="error"
          onClick={() => deleteBook(params.row.id)}
        >
          <DeleteIcon />
        </IconButton>
      )
    }
  ];

  return (
<div className="App">
      <AppBar position="static">
        <Toolbar>
          <Typography variant="h4">
            Bookstore
          </Typography>
        </Toolbar>
      </AppBar>
      <form style={{ display: 'flex', justifyContent: 'center', margin: '20px' }}>
  <TextField name="title" label="Title" placeholder="Title" value={newBook.title} onChange={(e) => setNewBook({ ...newBook, title: e.target.value })} />
  <TextField name="author" label="Author" placeholder="Author" value={newBook.author} onChange={(e) => setNewBook({ ...newBook, author: e.target.value })} />
  <TextField name="year" label="Year" placeholder="Year" value={newBook.year} onChange={(e) => setNewBook({ ...newBook, year: e.target.value })} />
  <TextField name="isbn" label="ISBN" placeholder="ISBN" value={newBook.isbn} onChange={(e) => setNewBook({ ...newBook, isbn: e.target.value })} />
  <TextField name="price" label="Price" placeholder="Price" value={newBook.price} onChange={(e) => setNewBook({ ...newBook, price: e.target.value })} />
  <Button variant="contained" color="primary" onClick={addBook}>Add Book</Button>
  <Button variant="contained" color="error" onClick={clearBooks}>Clear All Books</Button>
</form>
      <div style={{ height: 350, width: '100%', maxWidth: '1300px', margin: 'auto'}}>
        <DataGrid
          rows={books}
          columns={columns}
        />
        
      </div>

      </div>
  );
}

export default App;